.header {
  position: fixed;
  z-index: 100;
  width: 100%;
  &-top {
    background-color: $gray;
    &__wrapper {
      display: flex;
      align-items: center;
      padding: 12px 0;
      justify-content: space-between;
    }
    &__menu {
      align-items: center;
      display: none;
      @include screen(laptop) {
        display: flex;
      }
    }
    &__links {
      align-items: center;
      gap: 30px;
      margin-right: 32px;
      display: none;
      @include screen(laptop) {
        margin-right: 56px;
        display: flex;
      }
    }
    &__icons {
      display: flex;
      align-items: center;
      gap: 30px;
      margin-right: 30px;
    }
  }
  &__links {
    align-items: center;
    gap: 20px;
    display: none;
    @include screen(laptop) {
      gap: 40px;
      display: flex;
    }
  }
  &__link {
    color: $white;
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 18px;
      line-height: 21px;
    }
    a {
      font-weight: 700;
      border-bottom: 1px solid transparent;
      @include screen(laptop) {
        transition: border-color $transition-default;
        &:hover {
          border-color: $white;
        }
      }
    }
    svg {
      margin-top: 3px;
    }
    &-sm {
      font-size: 14px;
      line-height: 19px;
      @include screen(laptop) {
        font-size: 15px;
        line-height: 24px;
      }
    }
  }
}