* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  font-family: 'Roboto';
  touch-action: manipulation;
}

html,
body {
  height: 100%;
  color: $text;

  font-family: 'Roboto';
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  width: 100%;
  height: 100%;

  background-color: $white;
}

.wrapper {
  margin: 0 auto;
  padding: 0 #{$container-padding};
  width: 100%;
  max-width: calc(#{$mobile-wrapper} + #{$container-padding * 2});
  @include screen(tablet) {
    max-width: calc(#{$screen-tablet} + #{$container-padding * 2});
  }
  @include screen(tablet-2x) {
    max-width: calc(#{$screen-tablet-2x} + #{$container-padding * 2});
  }
  @include screen(laptop) {
    max-width: calc(#{$screen-laptop} + #{$container-padding * 2});
  }
  @include screen(desktop) {
    max-width: calc(#{$screen-desktop} + #{$container-padding * 2});
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}

.button {
  &-border {
    color: $white;
    border: 1px solid $white;
    padding: 13px 17px;
    border-radius: 3px;
    font-size: 18px;
    line-height: 21px;
    font-weight: 700;
  }
  &-green {
    background-color: $green;
    padding: 6px 14px;
    border-radius: 3px;
    font-size: 14px;
    line-height: 16px;
    color: $white;
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $green-hover;
      }
    }
  }
}

.title {
  font-weight: 700;
  line-height: 40px;
  font-size: 28px;
  text-align: center;
  @include screen(laptop) {
    line-height: 50px;
    font-size: 36px;
  }
}

main {
  padding-top: 106px;
  @include screen(laptop) {
    padding-top: 155px;
  }
}