.pagination {
  &__links {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    @include screen(laptop) {
      gap: 20px;
    }
    a {
      font-size: 16px;
      line-height: 24px;
      display: flex;
      width: 20px;
      height: 28px;
      align-items: center;
      justify-content: center;
      border: 1px solid transparent;
      border-radius: 3px;
      &.active {
        border-color: $green;
      }
      @include screen(laptop) {
        font-size: 20px;
        line-height: 32px;
        width: 24px;
        height: 32px;
        transition: border-color $transition-default, background-color $transition-default, color $transition-default;
        &:hover {
          background-color: $green;
          border-color: $green;
          color: $white;
        }
      }
      &.prev,
      &.next {
        svg {
          path {
            @include screen(laptop) {
              transition: fill $transition-default;
            }
          }
        }
        @include screen(laptop) {
          &:hover {
            background-color: transparent;
            border-color: transparent;
            svg {
              path {
                fill: $green;
              }
            }
          }
        }
      }
    }
  }
}