.article {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 10px;
  &__image {
    width: 100%;
    img {
      object-fit: contain;
      width: 100%;
      border-radius: 6px;
    }
  }
  &__info {
    font-size: 14px;
    line-height: 22px;
    color: $light-gray;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__title {
    a {
      font-size: 24px;
      line-height: 38px;
      font-weight: 700;
      border-bottom: 2px solid transparent;
      @include screen(laptop) {
        transition: border-color $transition-default;
        &:hover {
          border-color: $text;
        }
      }
    }
  }
  &__description {
    font-size: 16px;
    line-height: 25px;
  }
}