.blog {
  padding: 32px 0;
  @include screen(laptop) {
    padding: 70px 0 80px;
  }
  &__categories {
    display: flex;
    align-items: center;
    gap: 15px;
    flex-wrap: wrap;
    margin-top: 32px;
    @include screen(laptop) {
      margin-top: 50px;
    }
    a {
      border-radius: 20px;
      border: 1px solid $green;
      padding: 6px 14px;
      color: $green;
      @include screen(laptop) {
        transition: color $transition-default, background-color $transition-default;
        &:hover {
          background-color: $green;
          color: $white;
        }
      }
      &.active {
        background-color: $green;
        color: $white;
      }
    }
  }
  &__items {
    margin-top: 32px;
    margin-bottom: 64px;
    display: grid;
    column-gap: 24px;
    row-gap: 24px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      row-gap: 60px;
      margin-top: 34px;
      margin-bottom: 80px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
}