.menu {
  background-color: $black;
  padding: 16px 0;
  @include screen(laptop) {
    padding: 35px 0;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__button {
    svg {
      width: 24px;
      height: 24px;
      @include screen(laptop) {
        width: auto;
        height: auto;
      }
    }
  }
}