.footer {
  background-color: $black;
  padding: 64px 0;
  @include screen(laptop) {
    padding: 70px 0 100px;
  }
  &__wrapper {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    @include screen(laptop) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__cols {
    display: flex;
    width: 100%;
    gap: 40px;
    margin-top: 40px;
    align-items: flex-start;
    flex-direction: column;
    @include screen(tablet) {
      flex-direction: row;
      gap: 64px;
      justify-content: space-between;
    }
    @include screen(laptop) {
      width: auto;
      margin-top: 0;
      gap: 120px;
    }
  }
  &__col {
    h3 {
      color: $light-gray;
      font-size: 24px;
      line-height: 28px;
      font-weight: 700;
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    gap: 18px;
    align-items: flex-start;
    margin-top: 26px;
  }
  &__link {
    color: $white;
    border-bottom: 1px solid transparent;
    @include screen(laptop) {
      transition: border-color $transition-default;
      &:hover {
        border-color: $white;
      }
    }
  }
  &__info {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    color: $white;
    h2 {
      margin-top: 28px;
      font-size: 18px;
      line-height: 28px;
      font-weight: 700;
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    a {
      margin-top: 13px;
      display: inline-block;
    }
    p {
      margin-top: 33px;
      font-size: 18px;
      line-height: 28px;
      font-weight: 700;
    }
  }
}
.copy {
  background-color: $gray;
  padding: 28px 0;
  &__wrapper {
    display: flex;
    gap: 32px;
    justify-content: space-between;
    flex-direction: column;
    @include screen(tablet) {
      flex-direction: row;
      align-items: center;
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    gap: 16px;
    @include screen(tablet) {
      gap: 22px;
      flex-direction: row;
      align-items: center;
    }
    a {
      font-size: 16px;
      line-height: 25px;
      color: $light-gray-200;
      @include screen(laptop) {
        transition: color $transition-default;
        &:hover {
          color: $white;
        }
      }
    }
  }
}