.breadcrumbs {
  background-color: $gray;
  padding: 10px 0;
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    a,
    span {
      display: inline-block;
      color: $white;
      font-size: 12px;
      line-height: 19px;
    }
    a {
      border-bottom: 1px solid transparent;
      @include screen(laptop) {
        transition: border-color $transition-default;
        &:hover {
          border-color: $white;
        }
      }
    }
  }
}